rjetaAudio',{staticClass:"color-acento-botones bg-color-white mb-3",attrs:{"texto":"Concepto de riesgo país","tiempo":"","audio":require('../../assets/curso/podcast/podcast1.mp3')}})],1)]),_vm._m(4)])])]),_vm._m(5),_vm._m(6),_c('div',{staticClass:"row align-items-center mb-5"},[_vm._m(7),_c('div',{staticClass:"col-lg-8"},[_c('LineaTiempoD',{staticClass:"color-primario large verde",attrs:{"data-aos":"fade-left"}},[_c('p',{attrs:{"numero":"1","titulo":"Estabilidad política e institucional"}},[_vm._v("Los gobiernos con instituciones sólidas, reglas claras y bajos niveles de corrupción, tienden a generar mayor confianza en los mercados financieros, dado que ofrecen previsibilidad y seguridad jurídica para las inversiones. En contraste, los países con conflictos internos, inestabilidad política o sistemas de gobierno con baja credibilidad, suelen enfrentar mayores niveles de riesgo, pues la incertidumbre sobre el futuro de sus políticas económicas desincentiva la inversión y puede generar fuga de capitales. Situaciones como golpes de Estado, cambios abruptos en la legislación económica, crisis de gobernabilidad o violaciones al Estado de derecho, incrementan la percepción de riesgo y afectan directamente la calificación del país en los mercados internacionales.")]),_c('p',{attrs:{"numero":"2","titulo":"Crecimiento económico"}},[_vm._v("Es otro factor clave que influye en la percepción del riesgo país. Un país con tasas sostenidas de crecimiento del Producto Interno Bruto (PIB), muestra una economía dinámica, con mayor capacidad para generar ingresos fiscales, reducir el desempleo y cumplir con sus obligaciones financieras. Cuando un país mantiene un crecimiento estable, los inversionistas perciben que hay oportunidades de negocio y que el Estado tendrá recursos suficientes para pagar su deuda externa. Sin embargo, cuando la economía entra en recesión o el crecimiento es insuficiente para cubrir las necesidades de financiamiento del gobierno, la confianza en la capacidad de pago del país, disminuye. En estos casos, los mercados internacionales pueden exigir tasas de interés más elevadas para compensar el riesgo, encareciendo el costo del financiamiento, tanto para el sector público como para el privado.")]),_c('p',{attrs:{"numero":"3","titulo":"La inflación y la estabilidad"}},[_vm._v("La inflación y la estabilidad del tipo de cambio también juegan un papel determinante en la evaluación del riesgo país. La inflación elevada deteriora el poder adquisitivo de la población y genera desconfianza en la moneda local, lo que puede llevar a la dolarización parcial de la economía o a la fuga de capitales. Una inflación descontrolada refleja problemas estructurales en la política económica y puede hacer que los inversionistas se alejen de un país debido a la incertidumbre sobre la estabilidad de precios. Asimismo, la volatilidad del tipo de cambio afecta la confianza de los mercados, especialmente cuando un país tiene una alta proporción de su deuda denominada en moneda extranjera. Una devaluación abrupta de la moneda nacional, puede encarecer los pagos de deuda externa y dificultar la obtención de financiamiento en los mercados internacionales.")])])],1)]),_c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1"},[_c('LineaTiempoD',{staticClass:"color-primario large verde",attrs:{"data-aos":"fade-right"}},[_c('p',{attrs:{"numero":"4","titulo":"Nivel de endeudamiento externo"}},[_vm._v("Un país con altos niveles de deuda en relación con su PIB, es percibido como más riesgoso, puesto que su margen de maniobra fiscal es reducido y su capacidad de pago depende, en gran medida, de la obtención de nuevos créditos. Cuando un país acumula demasiada deuda sin un plan claro de sostenibilidad fiscal, puede entrar en un círculo vicioso de financiamiento caro y creciente presión sobre su presupuesto. Si la deuda se vuelve insostenible, el país podría enfrentar un default soberano, lo que afectaría su calificación crediticia y su acceso a financiamiento futuro. Además, la dependencia excesiva de la deuda externa expone al país a riesgos adicionales, como el impacto de cambios en las tasas de interés internacionales o la reducción del acceso a capital en tiempos de crisis global.")]),_c('p',{attrs:{"numero":"5","titulo":"La gestión gubernamental y la política fiscal"}},[_vm._v("También desempeñan un papel fundamental en la determinación del riesgo país. Un gobierno que administra eficientemente sus recursos, mantiene un presupuesto equilibrado y aplica políticas económicas coherentes puede reducir la percepción de riesgo y mejorar su calificación crediticia. En cambio, una mala administración de los recursos públicos, déficits fiscales persistentes o políticas económicas inconsistentes pueden aumentar la incertidumbre sobre la capacidad del país para manejar sus finanzas. Los gobiernos que implementan medidas de ajuste fiscal sin una estrategia clara o que dependen excesivamente de la emisión de deuda para cubrir su gasto público, pueden perder la confianza de los inversionistas y enfrentar dificultades para acceder a financiamiento en términos favorables.")]),_c('p',{attrs:{"numero":"6","titulo":"Los factores externos"}},[_vm._v("También influyen significativamente en el riesgo país. La globalización financiera ha hecho que las economías sean más interdependientes, por lo que un país puede verse afectado por factores que escapan a su control, como cambios en las tasas de interés internacionales, crisis financieras globales o variaciones en los precios de los commodities. Las economías emergentes, en particular, son más vulnerables a estos efectos, porque muchas dependen de la exportación de materias primas y de la inversión extranjera para sostener su crecimiento. Una caída en los precios del petróleo, los metales o los productos agrícolas, puede reducir drásticamente los ingresos fiscales de un país y afectar su capacidad de pago, lo que incrementa su percepción de riesgo en los mercados internacionales.")]),_c('p',{attrs:{"numero":"7","titulo":"Calidad del sistema financiero y bancario"}},[_vm._v("Un sistema financiero sólido, con bancos bien capitalizados y regulaciones prudentes, contribuye a la estabilidad económica y reduce la probabilidad de crisis bancarias. En cambio, un sistema financiero débil, con instituciones vulnerables a shocks externos o problemas de liquidez, puede incrementar el riesgo de crisis económicas y generar incertidumbre sobre la capacidad del país para sostener su crecimiento. La salud del sector bancario también influye en la disponibilidad de crédito para las empresas y los consumidores, lo que a su vez afecta la inversión y el dinamismo económico.")])])],1),_vm._m(8)]),_vm._m(9),_vm._m(10),_c('p',[_vm._v("El riesgo país es un indicador clave que refleja la percepción de estabilidad económica y financiera de una nación, con efectos directos sobre la inversión, el acceso al crédito y el crecimiento económico.")]),_c('div',{staticClass:"bg-full-width bg-color-info"},[_c('div',{staticClass:"px-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8"},[_c('h2',{staticClass:"mb-4",attrs:{"data-aos":"flip-up"}},[_vm._v("Impacto del riesgo país en la economía ")]),_vm._m(11),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_3.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(12)])]),_vm._m(13)])])]),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-