os pueden implementar para mitigar estos efectos y fortalecer la confianza en sus economías, promoviendo un desarrollo sostenible y estable.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"anexo__texto"},[_c('p',[_c('strong',[_vm._v("Anexo. ")]),_vm._v(" Impacto del riesgo país en la economía.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/9.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":""}})])])}]

